import { Anchor, Button, Group, Paper, Text, Textarea } from "@mantine/core";
import { useForm } from "@mantine/form";
import { showNotification } from "@mantine/notifications";
import { useState } from "react";
import useProfileService from "../api/services/useProfileService";

const ProfileAbout = ({ description, privateProfile, ...props }) => {
  const [editMode, setEditMode] = useState();

  const form = useForm({
    initialValues: {
      description: description,
    },
  });

  const { updateProfile } = useProfileService();

  const updateDescription = async (values) => {
    try {
      await updateProfile(values);
      setEditMode(false);
    } catch (error) {
      showNotification({
        title: "Oops! Something went wrong.",
        color: "red",
      });
    }
  };

  const cancel = () => {
    form.reset();
    setEditMode(false);
  };

  let content;
  if (editMode) {
    content = (
      <form onSubmit={form.onSubmit(updateDescription)} onReset={cancel}>
        <Textarea
          my="sm"
          autosize
          minRows={2}
          maxRows={5}
          placeholder="Write a short description about you, your work or whatever you want."
          {...form.getInputProps("description")}
        />
        <Group position="right" mt="xl">
          <Button variant="outline" type="reset">
            Cancel
          </Button>
          <Button variant="filled" type="submit">
            Save
          </Button>
        </Group>
      </form>
    );
  } else {
    content = !!description ? (
      <>
        <Text size={"sm"} color="dimmed" mt={"xs"}>
          {description}
        </Text>
        {!!privateProfile && (
          <Anchor
            component="button"
            type="button"
            mt="md"
            onClick={() => setEditMode(true)}
          >
            <Text size="sm">Edit</Text>
          </Anchor>
        )}
      </>
    ) : (
      <Group grow>
        <Anchor
          component="button"
          type="button"
          my="md"
          onClick={() => setEditMode(true)}
        >
          Say something about you.
        </Anchor>
      </Group>
    );
  }

  return (
    <Paper withBorder {...props} py="xs" px="md">
      <Text size={"xs"} sx={{ textTransform: "uppercase" }} weight={700}>
        About
      </Text>

      {content}
    </Paper>
  );
};

export default ProfileAbout;
