import { Navigate, Route, Routes } from "react-router-dom";
import PrivateRoute from "./PrivateRoute";

import { Box } from "@mantine/core";
import Authentication from "../views/account/Authentication";
import ForgotPassword from "../views/account/ForgotPassword";
import ResetPassword from "../views/account/ResetPassword";
import VerifyAccount from "../views/account/VerifyAccount";
import Dashboard from "../views/Dashboard";
import PageNotFound from "../views/error/PageNotFound";
import Connect from "../views/profile/Connect";
import PrivateProfile from "../views/profile/PrivateProfile";
import PublicProfile from "../views/profile/PublicProfile";
import QR from "../views/qr/QR";
import Settings from "../views/settings/Settings";

const AppRoutes = () => {
  return (
    <Box pb="xl" mb="xl">
      <Routes>
        <Route element={<PrivateRoute />}>
          <Route element={<Dashboard />} path="/" exact />
          <Route element={<PrivateProfile />} path="/profile" />
          <Route element={<QR />} path="/profile/share" />

          <Route element={<Settings />} path="/settings" />
        </Route>

        <Route element={<Authentication />} path="/account/login" />
        <Route element={<Authentication />} path="/account/register" />
        <Route element={<VerifyAccount />} path="/account/verify" />
        <Route element={<ForgotPassword />} path="/account/forgot" />
        <Route element={<ResetPassword />} path="/account/reset" />

        <Route element={<PublicProfile />} path="/profile/:userId" />
        <Route element={<Connect />} path="/profile/connect/:profileId" />

        <Route path="/error/404" element={<PageNotFound />} />

        <Route path="*" element={<PageNotFound />} />
        <Route path="*" element={<Navigate from="*" to="/" />} />
      </Routes>
    </Box>
  );
};

export default AppRoutes;
