import { Box, createStyles, Group, Text } from "@mantine/core";
import { IconMapPin } from "@tabler/icons";
import React from "react";
import { getLocationDisplayName } from "../utils";

const useStyles = createStyles((theme) => ({
  title: {
    fontSize: 34,
    fontWeight: 900,
    [theme.fn.smallerThan("sm")]: {
      fontSize: 24,
    },
  },
}));

const PersonalInformation = ({ profile, privateProfile }) => {
  const { classes } = useStyles();

  let handle, title, location;

  if (profile.account.handle) {
    handle = `@${profile.account.handle}`;
  } else {
    if (privateProfile) {
      handle = <Text italic>No handle added yet</Text>;
    }
  }

  if (profile.account.title) {
    title = profile.account.title;
  } else {
    if (privateProfile) {
      title = <Text italic>No title specified</Text>;
    }
  }

  if (profile.account.location?.city || profile.account.location?.country) {
    location = (
      <Group noWrap spacing={4} mt={1}>
        <IconMapPin size={16} color="gray" />
        <Text size="sm" color="dimmed" lineClamp={1}>
          {getLocationDisplayName(profile.account.location)}
        </Text>
      </Group>
    );
  } else {
    if (privateProfile) {
      location = (
        <Text size="sm" color="dimmed" lineClamp={1} italic>
          No location specified
        </Text>
      );
    }
  }

  return (
    <>
      {profile && (
        <Box mt="sm">
          <Text size="xs" weight={700} color="dimmed">
            {handle}
          </Text>

          <Text size="xl" weight={500} className={classes.name} lineClamp={2}>
            {profile.account.firstName} {profile?.account.lastName}
          </Text>

          <Text size="md" color="dimmed" lineClamp={2}>
            {title}
          </Text>

          {location}
        </Box>
      )}
    </>
  );
};

export default PersonalInformation;
