import {
  Avatar,
  Group,
  ScrollArea,
  Table,
  Text,
  UnstyledButton,
} from "@mantine/core";
import dayjs from "dayjs";
import { useNavigate } from "react-router-dom";

export function ConnectionsList({ connections }) {
  const navigate = useNavigate();

  const rows = connections.map((connection) => (
    <tr key={connection.id}>
      <td>
        <UnstyledButton
          onClick={() => navigate(`/profile/${connection.accountId}`)}
        >
          <Group spacing="sm">
            <Avatar size={40} src={"https://i.pravatar.cc/600"} radius={40} />
            <div>
              <Text size="sm" weight={500}>
                {connection.firstName} {connection.lastName}
              </Text>
              <Text color="dimmed" size="xs">
                {connection.title}
              </Text>
            </div>
          </Group>
        </UnstyledButton>
      </td>
      <td>
        <Text size="sm">{dayjs(connection.date).fromNow()}</Text>
        <Text size="xs" color="dimmed">
          Connected
        </Text>
      </td>
      {/* <td>
        <Group spacing={0} position="right">
          <ActionIcon>
            <IconPencil size={16} stroke={1.5} />
          </ActionIcon>
          <Menu transition="pop" withArrow>
            <Menu.Target>
              <ActionIcon>
                <IconDots size={16} stroke={1.5} />
              </ActionIcon>
            </Menu.Target>
            <Menu.Dropdown>
              <Menu.Item icon={<IconMessages size={16} stroke={1.5} />}>
                Send message
              </Menu.Item>
              <Menu.Item icon={<IconNote size={16} stroke={1.5} />}>
                Add note
              </Menu.Item>
              <Menu.Item icon={<IconReportAnalytics size={16} stroke={1.5} />}>
                Analytics
              </Menu.Item>
              <Menu.Item
                icon={<IconTrash size={16} stroke={1.5} />}
                color="red"
              >
                Terminate contract
              </Menu.Item>
            </Menu.Dropdown>
          </Menu>
        </Group>
      </td> */}
    </tr>
  ));

  return (
    <ScrollArea style={{ width: "100%", height: "350px" }}>
      <Table verticalSpacing="md">
        <tbody>{rows}</tbody>
      </Table>
    </ScrollArea>
  );
}
