import { Text, Title } from "@mantine/core";
import { useAtom } from "jotai";
import { useEffect } from "react";
import useProfileService from "../api/services/useProfileService";
import { ConnectionsList } from "../components/ConnectionsList";
import { profile } from "../store/profile";

export default function Dashboard() {
  const [profileAtom] = useAtom(profile);

  const { getPrivateProfile } = useProfileService();

  useEffect(() => {
    getPrivateProfile();
  }, []);

  // TODO add banner to inform that the user needs to verify the account

  return (
    <>
      {profileAtom?.connections?.length ? (
        <>
          <Title order={1} size="h2" color="dimmed">
            Connections
          </Title>

          <ConnectionsList connections={profileAtom.connections} />
        </>
      ) : (
        <Text align="center" color="dimmed" weight={500}>
          This is where your connections will appear!
        </Text>
      )}
    </>
  );
}
